.cell {
  width: 50px;
  height: 50px;
  transition: all 0.15s ease;
  border: 1px solid lightblue;

  &:hover {
    border-color: rgb(154, 184, 212);
    background-color: rgb(195, 226, 255);
  }

  &--start {
    border-color: rgb(2, 170, 10);
    background-color: rgb(18, 218, 28);
  }

  &--start:hover {
    border-color: rgb(2, 170, 10);
    background-color: rgb(9, 129, 15);
  }

  &--end {
    border-color: rgb(185, 13, 13);
    background-color: rgb(228, 3, 3);
  }

  &--end:hover {
    border-color: rgb(185, 13, 13);
    background-color: rgb(167, 3, 3);
  }

  &--checking {
    border-color: rgb(184, 187, 3);
    background-color: rgb(251, 255, 15);
  }

  &--checking:hover {
    border-color: rgb(184, 187, 3);
    background-color: rgb(167, 170, 12);
  }

  &--visited {
    border-color: rgb(97, 196, 230);
    background-color: rgb(137, 255, 255);
  }

  &--visited:hover {
    border-color: rgb(97, 196, 230);
    background-color: rgb(87, 165, 165);
  }

  &--wall {
    border-color: rgb(55, 55, 55);
    background-color: rgb(88, 88, 88);
  }

  &--wall:hover {
    border-color: rgb(55, 55, 55);
    background-color: rgb(58, 58, 58);
  }

  &--path {
    border-color: rgb(218, 116, 0);
    background-color: rgb(255, 136, 0);
  }

  &--path:hover {
    border-color: rgb(180, 96, 0);
    background-color: rgb(223, 120, 3);
  }
}
